import React, { Component } from "react";
import { DataHolder } from "./../../../../Help/DataHolder";
import { InputText } from "primereact/inputtext";
import { Button } from "react-bootstrap";
import { DataTable } from "primereact/datatable";
import { useEffect } from "react";
import { useState } from "react";
import { Dropdown } from "primereact/dropdown";
import { Column } from "primereact/column";
import { Col, Row } from "react-bootstrap";
import { _showError } from "../../../../Pages/LayoutPage";

const options = [
  { label: "", value: "include" },
  { label: "", value: "equals" },
];

const InActivePages = (props) => {
  const [text, setText] = useState();
  const [title, setTitle] = useState();
  const [applyType, setApplyType] = useState();
  const [list, setList] = useState();
  const [ran, setRan] = useState();

  useEffect(() => {
    setList(DataHolder.Setting.InActivePages);
  }, [DataHolder.Setting.InActivePages]);

  return (
    <>
      <Row>
        <Col>
          <label> </label>
          <InputText
            value={text}
            onChange={(e) => {
              setText(e.target.value);
            }}
          />
        </Col>
   
        <Col>
          <label> </label>
          <br />
          <Dropdown
            value={applyType}
            options={options}
            onChange={(e) => {
              setApplyType(e.value);
            }}
            placeholder=" "
          />
        </Col>
      </Row>

      <br />

      <Button
        onClick={() => {
          if (!text  || !applyType) {
            _showError("  ");
            return;
          }

          if (!DataHolder.Setting.InActivePages) {
            DataHolder.Setting.InActivePages = [];
          }

          DataHolder.Setting.InActivePages.push({
            Text: text,
            Title: title,
            ApplyType: applyType,
            rn: Math.random(),
          });

          setText("");
          setTitle("");
          setApplyType("");
          setList(DataHolder.Setting.InActivePages);

          setRan(Math.random());
        }}
      >
        
      </Button>

      <hr />
      <DataTable
        value={list}
        paginator
        paginatorTemplate="CurrentPageReport FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink RowsPerPageDropdown"
        currentPageReportTemplate=" {first}  {last}  {totalRecords}"
        rows={10}
        rowsPerPageOptions={[10, 20, 50]}
      >
        <Column field="Text" header=" "></Column>
        <Column field="ApplyType" header=" "></Column>
      </DataTable>
    </>
  );
};

export default InActivePages;
