import React, {Component} from 'react';
import {TabMenu} from "primereact/tabmenu";
import Row from "react-bootstrap/cjs/Row";
import Col from "react-bootstrap/cjs/Col";
import {Card} from "primereact/card";
import {Menubar} from "primereact/menubar";
import {
    _GoCompaignAnalicts,
    _GoCompaignDefine,
    _GoCompaignTable
} from "../Manage/ChildComps/CompaginHelps/CompaignUtility";

 
import './CompaignFilterStyle.css';
import { _GoCompaignSubRoute } from './../Manage/ChildComps/CompaginHelps/CompaignUtility';
class CompaignFilter extends Component {
    constructor(props) {
        super(props);


        this.filters = [
            {
                label: ' ', icon: 'pi pi-fw pi-home',
                command: () => {

                    _GoCompaignDefine();
                }
            },
            {
                label: '  ', icon: 'pi pi-fw pi-home',
                command: () => {

                    _GoCompaignAnalicts('  ');
                }
            },
            {
                label: '  ', icon: 'pi pi-fw pi-home',
                command: () => {

                    _GoCompaignSubRoute('emailSetting','  ');
                }
            }
        
            ,{
                label: '  ', icon: 'pi pi-fw pi-home',

                items:[
                    {
                        label: '', icon: 'pi pi-fw pi-home',
                        command: () => {

                            _GoCompaignTable('all','');
                        }
                    } ,
                    {
                        label: '', icon: 'pi pi-fw pi-calendar',
                        command: () => {
                            _GoCompaignTable('automatic','');

                        }
                    },
                    {
                        label: '', icon: 'pi pi-fw pi-pencil',
                        command: () => {
                            _GoCompaignTable('manual','');

                        }
                    },
                ]
            },

            {
                label: '     ', icon: 'pi pi-fw pi-home',

                items:[

                    {
                        label: '  ', icon: 'pi pi-fw pi-home',
                        command: () => {
                            _GoCompaignTable('sending','  ');

                        }
                    },
                    {
                        label: ' ', icon: 'pi pi-fw pi-calendar',
                        command: () => {
                            _GoCompaignTable('readyToSend',' ');

                        }
                    },
                    {
                        label: '', icon: 'pi pi-fw pi-pencil',
                        command: () => {
                            _GoCompaignTable('stoped','');

                        }
                    },
                    {
                        label: ' ', icon: 'pi pi-fw pi-pencil',
                        command: () => {
                            _GoCompaignTable('notConfigured',' ');

                        }
                    },
                    {
                        label: ' ', icon: 'pi pi-fw pi-pencil',
                        command: () => {
                            _GoCompaignTable('sent',' ');

                        }
                    }
                ]
            },
           
           
        ];
    }


    render() {
        return (
            <Card>
                <Row>
                  {/*  <Col md={4}>
                        <Menubar model={this.items}/>
                    </Col>*/}
                    <Col>
                        <Menubar model={this.filters}/>
                    </Col>

                </Row>
            </Card>

        );
    }
}

export default CompaignFilter;